import React from "react";
import {Table} from "reactstrap";

export default ({className, income, expense}) => <Table>
    <tbody>
    <tr>
        <td>:</td>
        <td align="right">{income} </td>
    </tr>
    <tr>
        <td>C:</td>
        <td align="right">{income - expense} </td>
    </tr>
    </tbody>
</Table>


